
import header from '../public/header'
import footer from '../public/footer'
import consult from '../public/consult'
import nav from '../public/nav'
import towBanner from '../../assets/towBanner.jpg'

import service_list from '../../assets/service_list.png'
import service_list2 from '../../assets/service_list2.png'
import Panel from 'vux/src/components/panel/index.vue'


export default {
	
  components: {
  	headerV : header,
  	footerV : footer,
  	consultV : consult,
  	navV : nav, Panel
  },
  data () {
    return {
    	banner : [
    		{
    			src : towBanner,
    			desc : ' '
    		}
    	],
    	drawing : [
    		{
    			title : '',
    			desc : ''
    		},
    	],
    	D : [
    		{
    			desc : '3D3D3D Street Painting 3D',
				desc2 :	'3D3D3D3D3D'
    		}
    	],
    	painting : [
    		{
    			desc : '',
    			desc2 : ''
    		}
    	],
    	relief : [
    		{
    			desc : '',
    			desc2 : ''
    		}
    	],
    	type : '1',
    	list: [
    		{
		       src: service_list,
		       title: '',
		       desc: '',
		    }, 
		    {
		       src: service_list2,
		       title: '',
		       desc: 'KTV',
		    },
		    {
		       src: service_list2,
		       title: '',
		       desc: '',
		    }
      	],
    }
  },
  mounted () {
  	setTimeout(()=>{	
  		this.$refs.a[0].style.right="0"
  	},300)
  }
}




